// Pathify
import { make } from 'vuex-pathify'
import { config } from '@/plugins'
import axios from 'axios'
import _ from 'lodash'

const http = axios.create({
  headers: {
    'Content-Type': 'application/json',
  },
})

const api = config.baseApi + '/api/v1/contacts'

const loadContacts = ({ commit, state }, { limit, filter, reload }) => {
  const from = state.lastFilter == filter && !reload ? state.contacts.length : 0
  if (from == 0 || state.lastCountLoad >= state.lastLimit) {
    commit('loading', from > 0)
    http.get(api, { params: { from, limit, q: filter } }).then((res) => {
      commit('contact', res.data.contact)
      commit('contacts', from == 0 ? res.data.contacts : [...state.contacts, ...res.data.contacts])
      commit('lastCountLoad', res.data.contacts && res.data.contacts.length || 0)
      commit('lastFilter', filter)
      commit('lastLimit', limit)
      commit('apiService', true)
    }).finally(() => {
      commit('loading', false)
    }).catch(() => {
      commit('apiService', false)
    })
  }
}

const state = {
  contacts: [],
  contact: null,
  loading: false,
  lastCountLoad: null,
  lastLimit: null,
  lastFilter: null,
  apiService: true,
}

const mutations = make.mutations(state)

const actions = {
  loadContacts: _.debounce(loadContacts, 500),
  loadMoreContacts: loadContacts,
}

const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}